import {
    EditorButton,
    LimelProsemirrorAdapterCustomEvent,
} from '@limetech/lime-elements';
import { Component, h, State } from '@stencil/core';
/**
 * Example with custom menu
 */
@Component({
    tag: 'limel-example-prosemirror-adapter-with-custom-menu',
    shadow: true,
})
export class ProsemirrorAdapterWithCustomMenuExample {
    @State()
    private text: string = '';

    public render() {
        const menuItems: EditorButton[] = [
            'strong',
            'emphasis',
            'heading 1',
            'heading 2',
            'heading 3',
            'undo',
            'redo',
            'bullet list',
            'ordered list',
            'block quote',
            'join above',
            'lift out',
        ];

        return [
            <limel-prosemirror-adapter
                onChange={this.handleChange}
                menuItems={menuItems}
            />,
            <limel-example-value value={this.text} />,
        ];
    }

    private handleChange = (
        event: LimelProsemirrorAdapterCustomEvent<string>,
    ): void => {
        event.stopPropagation();

        this.text = event.detail;
    };
}
